const mutations = {
  changeMusic (state, obj) {
    state.songname = obj.songname;
    state.singer = obj.singer;
    state.musicUrl = obj.musicUrl;
    state.musicPic = obj.musicPic;
    state.lyric = obj.lyric;
  }
};
export default mutations



