<template>
  <swiper :options="swiperOption2" id="songslider">
    <!-- slides -->
    <swiper-slide class="songwrap">
      <header>
        <div>—— {{singer}} ——</div>
      </header>
    </swiper-slide>
    <swiper-slide class="songwrap">
      2222222
    </swiper-slide>
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper>

</template>

<script>
  import { mapState, mapMutations } from 'vuex'
  export default {
    name: 'songdetail',
    components: {
    },
    data () {
      return {
        swiperOption2: {
          onSlideChangeStart (swiper) {
            // alert(swiper.activeIndex);// 切换结束时，告诉我现在是第几个slide
            let pic = document.querySelectorAll('#btPic')[0];
            if (swiper.activeIndex !== 0) {
              pic.style.display = 'none'
            } else {
              pic.style.display = 'block'
            }
          },
          pagination: '.swiper-pagination',
          paginationClickable: true
        }
      }
    },
    created () {
    },
    mounted  () {
    },
    methods: {
      ...mapMutations(['changemusic'])
    },
    computed: {
      ...mapState(['songname', 'singer', 'musicUrl', 'musicPic', 'lyric'])
    }
  }
</script>
<style rel="stylesheet/less" lang="less">
@import "../assets/less/var.less";
#songslider{
  flex: 1;
  margin: 0;
  .songwrap{
    color: #fff;
    header{
      div{
        font-size: .75rem;
      }
    }
  }
  .swiper-pagination-bullet-active{
    background: #fff !important;
    opacity: 1;
  }
}
</style>



